.container {
  display: flex;
}

.avatar {
  padding: 15px 15px;
}
.avatar img {
  height: 150px;
  border: solid black 1px;
  border-radius: 50%;
}

.message {
  width: 200px;
  height: 75px;
  background: #1976d2;
  border-radius: 4px;
  box-shadow: 2px 8px 5px #000;
  position: relative;
  margin: 50px 0 25px;
}
.message:before {
  content: "";
  position: absolute;
  background-color: transparent;
  bottom: -50px;
  height: 50px;
  width: 25px;
  border-top-left-radius: 25px;
  box-shadow: 0 -25px 0 0 #1976d2;
}
.message .message__name {
  padding: 5px;
  font-size: 1.5em;
}
.message .message__text {
  padding-left: 5px;
  font-size: 1.2em;
  letter-spacing: 0.05em;
  position: relative;
}
.message .message__time {
  font-size: 1.2em;
  position: absolute;
  bottom: 8px;
  right: 10px;
  text-transform: uppercase;
}

